// Декоративные элементы

// метки для документов
.mark {
  font-size: 0.625em;
  color: @color-text-light;
  user-select: none;
  &_pdf,
  &_doc,
  &_xls {
    display: inline-block;
    width: 30px;
    height: 13px;
    text-align: center;
    vertical-align: middle;
  }
  &_pdf {
    background: #FC2130;
  }
  &_doc {
    background: #5B94D6;
  }
  &_xls {
    background: #55B74F;
  }
}

// тулбар
.toolbar {
  position: relative;
  padding: 0 6px;
  margin-left: -6px;
  cursor: pointer;
  &:hover {
    .toolbar__icon,
    .toolbar__icon::before,
    .toolbar__icon::after {
        background: @color-buttons;
    }
    .toolbar__act {
      opacity: 1;
      height: auto;
      width: auto;
      li {
        opacity: 1;
        .transition;
        &:hover {
          background: @color-element-light-primary;
        }
      }
    }
  }
}

.toolbar__icon {
  position: relative;
  display: inline-block;
  width: 3px;
  height: 3px;
  background: @color-text-gray;
  border-radius: 50%;
  .transition;
}

.toolbar__icon::before,
.toolbar__icon::after {
  content: '';
  .toolbar__icon;
  position: absolute;
}

.toolbar__icon::before {
  top: 6px;
}

.toolbar__icon::after {
  bottom: 6px;
}

.toolbar__act {
  position: absolute;
  top: 0;
  right: 15px;
  margin: 0;
  background: @color-element-white;
  box-shadow: 0 0 25px fadeout(@color-buttons,75%);
  z-index: 1;
  opacity: 0;
  overflow: hidden;
  height: 0;
  width: 0;
  padding: 5px 0;
  .radius;
  .transition;
  li {
    padding: 5px 15px;
    font-size: 0.750em;
    list-style-type: none;
    opacity: 0;
  }
}



// чекбокс персонального кабинета
.checkbox {
  display: none;
}

.toggle {
  position: relative;
  display: flex;
  align-items: center;
  width: 41px;
  height: 18px;
  background: @color-forms-stroke-standart;
  border-radius: 50px;
  padding: 4px 4px;
  margin-left: 10px;
  text-align: left;
  cursor: pointer;
  transition: 0.25s;
}

.disc {
  display: inline-block;
  position: relative;
  background: @color-forms-stroke-active;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  left: 0;
  transform: rotate(0);
  transition: 0.3s;
}

.checkbox:checked + .toggle {
  .disc {
    background: @color-active;
    left: 25px;
    transform: rotate(110deg);
  }
}

// radio_family

.radio {
  display: none;
}

.radio_family {
  label {
    display: inline-block;
    float: left;
    padding: 7px 20px;
    font-size: 0.750em;
    color: @color-element-dark-primary;
    border: 1px solid @color-buttons;
    .transition;
    &:first-of-type {
      border-radius: 6px 0 0 6px;
      border-right: none;
    }
    &:last-of-type {
      border-radius: 0 6px 6px 0;
      border-left: none;
    }
    &:hover {
      color: @color-buttons-hover;
    }
  }
  .radio:checked + label {
    color: @color-text-light;
    background: @color-buttons;
  }
}
