// Ссылка на документ
.document__link {
  position: relative;
  .flex-center-between;
  &:nth-of-type(2n) {
    margin: 5px 0;
  }
  .link {
    color: @color-text-gray;
    margin-left: 5px;
  }
}

// Modules bg color
.module_primary {
  background: @color-element-white;
  box-shadow: 0 0 25px fadeout(@color-buttons,85%);
}
.module_secondary {
  background: @color-element-light-secondary;
  box-shadow: 0 0 25px fadeout(@color-buttons,95%);
}

// Modules default
.module {
  height: fit-content;
  .radius;
  // header
  &__header {
    .flex-center-between;
    padding: 20px;
    h5 {
      margin: 0;
    }
    span {
      font-size: 0.750em;
      &:not([class]) {
        color: @color-text-gray-light;
      }
      &.mark_user {
        padding: 2px 8px;
        color: @color-element-white;
        background: @color-forms-primary;
        border-radius: 999px;
      }
    }
  }
  // content
  &__content {
    padding: 0 20px;
    padding-bottom: 20px;
    .row,
    .row > .row__content {
      &:nth-of-type(2n) {
        margin: 15px 0;
      }
      &:last-child {
        margin-bottom: 0;
      }
      & > span {
        font-size: 0.875em;
        margin-left: 40%;
        &:first-child {
          color: @color-text-gray;
          margin-left: 0;
        }
        &:nth-child(2) {
          margin-left: 0;
        }
      }
    }
    .row__content {
      display: flex;
      flex-grow: 1;
    }
    .col {
      &:nth-of-type(2n) {
        margin: 0 20px;
      }
      span {
        font-size: .875em;
      }
    }
    .col-xs {
      &:first-of-type {
        padding-left: 0;
      }
      &:last-of-type {
        padding-right: 0;
      }
    }
    // Развернуть всю информацию
    .expand {
      margin: 15px 0;
      margin-bottom: 0;
      padding: 5px;
      text-align: center;
      cursor: pointer;
      span {
        display: inline-block;
        font-size: 0.750em;
        text-transform: uppercase;
        color: @color-text-gray;
        .transition;
        &:hover {
          color: @color-text-dark;
          span::after {
            border-top-color: @color-text-dark;
          }
        }
        &::after {
          content: '';
          position: relative;
          top: 6px;
          border: 3px solid;
          border-bottom-width: 0;
          border-left-color: transparent;
          border-right-color: transparent;
          border-top-color: @color-text-gray;
          margin-left: 10px;
          .transition;
        }
      }
    }
  }
  // module__footer
  &__footer {
    .flex-middle;
    padding: 20px;
    padding-top: 0;
    &_border {
      padding: 20px;
      border-top: 1px solid @color-element-light-primary;
    }
    span {
      &:nth-of-type(2n) {
        margin-left: 20px;
      }
    }
    //
    svg {
      position: relative;
      top: 2px;
      height: 14px;
      width: 14px;
      margin-right: 5px;
      fill: @color-element-dark-primary;
      .transition;
    }
    a {
      &:last-child {
        margin-left: 20px;
      }
    }
    //
  }

  &-user,
  &-contacts {
    .row,
    .row > .row__content {
      & > span {
        width: 60%;
        &:first-child {
          width: 40%;
        }
      }
    }
  }
  // user
  &-user,
  &-new-contractor {
    .col {
      span {
        &:first-child {
          font-size: 0.6875em;
          color: @color-text-gray-light;
        }
      }
    }
    .row {
      .row {
        margin-left: 40%;
        &:first-of-type {
          margin-left: 0;
        }
      }
    }
  }
}

.module-project,
.module-money,
.module-documents {
  .row {
    span {
      &:nth-child(2) {
        font-weight: bold;
        color: @links-link;
      }
    }
  }
}

// комментарии

.comments {
  padding: 20px;
}

.comment {
  margin-bottom: 20px;
  &__header {
    .flex-center-between;
    margin-bottom: 10px;
    span {
      font-size: 0.750em;
      color: @color-text-gray-light;
    }
  }
  &__text {
    font-size: 0.750em;
    margin: 0;
  }
}

.amount {
  grid-column: span 2;
  &-total {
    margin-left: 10px;
    font-weight: bold;
    color: @color-element-dark-secondary;
  }
}

// Акты сверки
.module-revise {
  .module__content {
    padding: 0;
    padding-bottom: 20px;
    .row {
      padding: 0 20px;
      &:nth-of-type(2n) {
        padding: 20px;
        border-top: 1px solid @color-forms-stroke-standart;
        border-bottom: 1px solid @color-forms-stroke-standart;
      }
      &:last-child {
        border-bottom: none;
        padding-bottom: 0;
      }
    }
    .col {
      margin: 0;
      &:nth-of-type(1) {
        span {
          &:first-child {
            color: @links-link;
            font-weight: bold;
          }
          &:nth-child(2) {
            color: @color-text-gray-light;
            font-size: 0.750em;
          }
        }
      }
      &:nth-of-type(2) {
        span {
          font-size: 0.750em;
          background: @color-element-light-primary;
          padding: 10px;
          span {
            font-size: inherit;
            font-weight: bold;
            margin-left: 10px;
          }
        }
      }
    }
  }
}

.module-personal {
  .module__header {
    svg {
      position: relative;
      top: 3px;
      width: 14px;
      height: 14px;
      margin-left: 5px;
      fill: @color-text-gray-light;
      transform: rotate(180deg);
    }
  }
}

// новый контрагент

.module-new-contractor {
  .module__content {
    .row {
      .flex-between;
      input[type="text"],
      textarea {
        font-size: 0.750em;
        width: 60%;
      }
      .col {
        width: 100%;
        &:nth-of-type(2n) {
          margin: 0;
          margin-left: 10px;
        }
        input[type="text"] {
          width: 100%;
        }
      }
      .row {
        width: 60%;
        flex-wrap: nowrap;
      }
    }
    .row-top-xs {
      span {
        margin-top: 10px;
      }
    }
  }
}
